.header {
  padding: 0 15px;
  width: 100%;
  height: 100vh;
  background-color: #fff;

  &__top {
    display: flex;
    justify-content: space-between;
    height: 80px;
    align-items: center;
  }
  &__caption {
    font-weight: 800;
    font-size: 36px;
    color: #181818;
    line-height: 58px;
    text-align: center;
  }
}
.nav {
  &__toggle {
    position: relative;
    width: 32px;
    height: 32px;
    background-color: transparent;
    border: 0;
    padding: 0;

    &:hover {
      & .nav__toggle-item:nth-child(2) {
        transform: translateX(-100%) translateY(-50%);
        opacity: 0;
      }
      & .nav__toggle-item:nth-child(1) {
        transform: translateX(-50%) translateY(-50%) rotate(45deg);
      }
      & .nav__toggle-item:nth-child(3) {
        transform: translateX(-50%) translateY(-50%) rotate(-45deg);
      }
    }
  }
  &__toggle-item {
    position: absolute;
    display: block;
    width: 24px;
    height: 4px;
    background-color: #000;
    border-radius: 4px;
    left: 50%;
    top: 50%;
    transition: all 0.2s ease;
    transform: translateX(-50%) translateY(-50%);
    &:first-child {
      transform: translateX(-50%) translateY(calc(-50% - 8px));
    }
    &:last-child {
      transform: translateX(-50%) translateY(calc(-50% + 8px));
    }
  }
}
